@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,600,700);
@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,600,700);
body{font-family:"Quicksand",sans-serif;font-size:16px;line-height:1.6;font-weight:400;color:#252525}h1{font-family:"Quicksand",sans-serif;color:#fff;font-size:3rem;font-weight:700}h2{font-family:"Quicksand",sans-serif;color:#217384;font-size:2rem;font-weight:700;margin-top:1rem}h3{font-family:"Quicksand",sans-serif;color:#217384;font-size:2.5rem;text-align:center;transform:translateY(0px);transition:all .5s;font-weight:600;margin-bottom:2rem;padding-top:5rem}h3:hover{transform:translateY(-3px)}h4{font-family:"Quicksand",sans-serif;color:#952092;font-size:1.5rem;font-weight:bold;margin:0 0 0.3rem 0}h5{font-family:"Quicksand",sans-serif;color:#093980;font-size:1.25rem;font-weight:600;margin:0 0 0.3rem 0}h6{font-family:"Quicksand",sans-serif;font-size:1.1rem;color:dimgray;font-style:italic}.block{padding:2rem}.block .TwoColumns{display:grid;grid-template-columns:1fr 3fr;grid-gap:3rem;grid-row-gap:0.5em;word-break:break-word}.block .TwoColumns .FirstColumn .logo{max-width:5rem;float:right}.block .TwoColumns .FirstColumn .icon{float:right;color:#093980;font-size:40px}.block .list::before{color:#952092;content:"✽ "}.block .MobileColumn{display:none;margin-top:0.5rem}.block:nth-of-type(2) .TwoColumns .FirstColumn .icon{color:#217384}.block:nth-of-type(3) .TwoColumns .FirstColumn .icon{color:#5e1cad}.block:nth-of-type(4) .TwoColumns .FirstColumn .icon{color:#42b7ce}hr{margin:0 !important}.HorizontalLine{padding:0 0 1.5rem 0;clear:both}a[href]:hover h6{color:#217384;font-weight:600}.skills-bars{display:grid;grid-template-columns:1fr 1fr 1fr;grid-auto-flow:row;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem}@media only screen and (max-width: 768px){.block{padding:2rem 0rem}.block .TwoColumns{grid-gap:1rem}.block .TwoColumns .SecondColumn .description{display:none}.block .MobileColumn{display:block;padding:0 10%}}@media only screen and (max-width: 500px){.block .MobileColumn{padding:0}.skills-bars{grid-template-columns:1fr}}

#Home{background:#37ced5;padding-top:10rem;padding-bottom:5rem}#Home #navbar{background-color:#37ced5;margin-bottom:5rem}#Home #navbar .navbar-brand{color:#fff;font-weight:bold;font-size:24px;letter-spacing:1.25px}#Home #navbar .nav-link{color:#fff;font-weight:bold;font-size:18px;letter-spacing:1.25px;text-decoration:none;padding:1rem}#Home #navbar .nav-link:hover{color:#217384}#Home #navbar .navbar-toggler{border:none}#Home .header-title{text-align:center;padding-left:2rem;padding-right:2rem}#Home .header-title .profile{width:20%;height:auto;border-radius:50%;border:0.25rem solid #fff}#Home .header-title .header-icons{margin-top:2rem}#Home .header-title .header-icons a{transform:translateY(0px);transition:all 1s}#Home .header-title .header-icons a img{width:auto;height:2rem}#Home .header-title .header-icons a:not(:first-of-type){margin-left:2rem}#Home .header-title .header-icons a :hover{transform:translateY(-5px)}#Home .header-about{border-radius:3rem;margin:auto;margin-top:4rem;padding:1.5rem 2rem;background-color:#fff;color:#217384;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);width:70%;position:relative;text-align:center}#Home .header-about::after{content:' ';position:absolute;width:auto;height:auto;left:48%;right:auto;top:-30px;bottom:auto;border:15px solid;border-color:transparent transparent #fff}@media screen and (max-width: 768px){#Home .header-title .profile{width:30%}#Home .header-about{width:85%}}@media screen and (max-width: 576px){#Home .header-title .profile{width:50%}}

